<template>
  <div>
    <div style="margin: auto">
      <el-input v-model="search" placeholder="" style="width: 10%; margin: auto" clearable/>
      <el-button type="primary" style="margin: auto" @click="load"></el-button>
    </div>
    <div class="home" style="display: flex; padding: 10px">
<!--      filter    -->
      <el-table :data="tableData.filter((v) => v.sno !== 1 && v.deleted !== 1)" style="width: 100%" >
        <el-table-column prop="sno" label="" />
        <el-table-column prop="syear" label="" />
        <el-table-column prop="sname" label=""/>
        <el-table-column prop="sgrade" label=""/>
        <el-table-column prop="stotal" label=""/>
        <el-table-column prop="isprice" label=""/>
        <el-table-column prop="sinfo" label=""/>
        <el-table-column fixed="right" label="">
          <template #default="scope">
            <el-button link type="primary" size="small" @click="handleEdit(scope.row)"></el-button>
            <el-popconfirm title="?" @confirm="handleDelete(scope.row.id)">
              <template #reference>
                <el-button link type="primary" size="small"></el-button>
              </template>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>


    </div>
    <div style="margin: 10px; padding: 10px; margin: auto">
      <el-button type="primary" style="margin: 10px; margin: auto" @click="add"></el-button>
    </div>

    <div class="demo-pagination-block">
      <div class="demonstration"></div>
      <el-pagination
          v-model:current-page="currentPage"
          v-model:page-size="pageSize"
          :page-sizes="pageSize"
          :small="small"
          layout="total, sizes, prev, pager, next, jumper"
          :total=total
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>

    <el-dialog
        v-model="dialogVisible" title="" width="30%">
      <el-form :model="form" label-width="120px">
        <el-form-item label="">
          <el-input v-model="form.sno" style="width: 80%; height: 80%" ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.syear" style="width: 80%; height: 80%" ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.sname" style="width: 80%; height: 80%"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.sgrade" style="width: 80%; height: 80%"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.stotal" style="width: 80%; height: 80%" disabled></el-input>
        </el-form-item>
        <el-form-item label="">
          <div class="mb-2 flex items-center text-sm">
            <el-radio v-model="form.isprice" label="" size="large"></el-radio>
            <el-radio v-model="form.isprice" label="" size="large"></el-radio>
            </div>
        </el-form-item>
      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogVisible = false"></el-button>
        <el-button type="primary" @click="save"></el-button>
      </span>
      </template>
    </el-dialog>
  </div>
</template>




<script>


import request from "@/utils/request";
import moment from 'moment';

export default {
  name: 'student',
  components: {},
  data() {
    return {
      dialogVisible: false,
      form: {},
      search: "",
      tableData: [],
      currentPage: 1,
      pageSize: 10,
      total: 0,
    }
  },
  created(){ //
    this.load()
  },
  methods: {
    add(){
      this.dialogVisible = true
      this.form = {}
      this.load()
    },

    save() { //
      if (this.form.id) {//formid
        const userInfo = sessionStorage.getItem('student');
        const operator = userInfo ? JSON.parse(userInfo).sname : null;
        console.log(operator); // 
        this.form.operator = operator;
        const currentTime = new Date();

        this.form.operator = operator;
        this.form.operatetime = currentTime;

        request.put("/student", this.form).then(res => {
          console.log(res)
          if (res.code === '0') {
            this.$message({type: "success", message: ""})
          } else {
            this.$message({type: "error", message: ""})
          }
        })
      } else {
        const userInfo = sessionStorage.getItem('student');
        const operator = userInfo ? JSON.parse(userInfo).sname : null;
        console.log(operator); // 
        this.form.operator = operator;
        const currentTime = new Date();

        this.form.operator = operator;
        this.form.operatetime = currentTime;

        request.post("/student", this.form).then(res => {
          console.log(res)
          if (res.code === '0') {
            this.$message({type: "success", message: ""})
          } else {
            this.$message({type: "error", message: ""})
          }
        })
      }
    },

    load(){
      request.get("/student",{
        params: {
          pageNum: this.currentPage,
          pageSize: this.pageSize,
          search: this.search
        }
      }).then(res =>{console.log(res)
      this.tableData = res.data.records
      this.total = res.data.total
      })
    },

    handleEdit(row){
      this.form = JSON.parse(JSON.stringify(row)) //
      this.dialogVisible = true
    },

    handleDelete(id){
      console.log(id)
      //requesturlidUserController@PathVariable/{id}
      request.delete("/student/" + id).then(res => {
        if(res.code === '0'){
          this.$message({type: "success", message:""})
        }
        else {
          this.$message({type: "error", message:""})
        }
        this.load() //
      })
    },

    handleSizeChange(){
      this.load() //
    },
    handleCurrentChange(){
      this.load() //
    },

  }
}

</script>
