<template>
  <el-form :model="form" label-width="120px" style="margin: auto">
    <el-form-item label="">
      <el-select v-model="form.sgrade" placeholder="">
        <el-option label="2023" value="" @click="dialogVisible1=true"/>
        <el-option label="2023" value="" @click="dialogVisible2=true"/>
        <el-option label="2023" value="" @click="dialogVisible3=true"/>
      </el-select>
    </el-form-item>
  </el-form>

  <el-card style="width: 10%;height: 80%; margin-inside: auto" shadow="always">
    <el-form ref="form" :model="person" label-width="80 px">
      <el-form-item label=" ">
        <el-input v-model="person.sno" disabled></el-input>
      </el-form-item>
      <el-form-item label=" ">
        <el-input v-model="person.sname" disabled></el-input>
      </el-form-item>
      <el-form-item label=" ">
        <el-input v-model="person.syear" disabled></el-input>
      </el-form-item>
      <el-form-item label=" ">
        <el-input v-model="person.sgrade" disabled></el-input>
      </el-form-item>
    </el-form>
  </el-card>
  <div>
<!-- -->
    <el-dialog v-model="dialogVisible1" title="" width="30%">
      <el-form :model="form1" label-width="120px">
        <el-form-item label="">
          <el-input  style="width: 220px; height: 80%" v-model="form1."/>
        </el-form-item>
        <el-form-item label="">
          <el-input-number v-model="form1." :min="1" :max="100" @change="handleChange" />
        </el-form-item>
      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogVisible1 = false"></el-button>
        <el-button type="primary" @click="save1"></el-button>
      </span>
      </template>
    </el-dialog>
<!---->
    <el-dialog
        v-model="dialogVisible2" title="" width="30%">
      <el-form :model="form2" label-width="120px" :rules="rules">
        <el-form-item label="">
          <el-input  style="width: 220px; height: 80%" v-model="form2."/>
        </el-form-item>
        <el-form-item label="">
          <el-input-number v-model="form2." :min="1" :max="100" @change="handleChange" />
        </el-form-item>
        <div></div>
        <el-form-item label="">
          <el-select v-model="form2.." placeholder="">
            <el-option label="" value="" />
            <el-option label="" value="" />
            <el-option label="" value="" />
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form2.." placeholder="">
            <el-option label="" value=""/>
            <el-option label="" value=""/>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input-number v-model="form2.." :min="0" :max="100" @change="handleChange" />
        </el-form-item>
        <div></div>
        <el-form-item label="">
          <el-select v-model="form2.." placeholder="">
            <el-option label="CCFA" value="CCFA"/>
            <el-option label="CCFB" value="CCFB"/>
            <el-option label="" value=""/>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form2.." placeholder="">
            <el-option label="" value=""/>
            <el-option label="" value=""/>
            <el-option label="" value=""/>
          </el-select>
        </el-form-item>
      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogVisible2 = false"></el-button>
        <el-button type="primary" @click="save2"></el-button>
      </span>
      </template>
    </el-dialog>
<!--    -->
    <el-dialog
        v-model="dialogVisible3" title="" width="30%" >
      <el-form :model="form3" label-width="120px">
        <div></div>
        <el-form-item label="">
          <el-select v-model="form3.." placeholder="">
            <el-option label="" value="" />
            <el-option label="" value="" />
            <el-option label="" value="" />
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form3.." placeholder="">
            <el-option label="" value=""/>
            <el-option label="" value=""/>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input-number v-model="form3.." :min="0" :max="100" @change="handleChange" />
        </el-form-item>
        <div></div>
        <el-form-item label="">
          <el-select v-model="form3.." placeholder="">
            <el-option label="CCFA" value="CCFA"/>
            <el-option label="CCFB" value="CCFB"/>
            <el-option label="" value=""/>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form3.." placeholder="">
            <el-option label="" value=""/>
            <el-option label="" value=""/>
            <el-option label="" value=""/>
          </el-select>
        </el-form-item>
      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogVisible3 = false"></el-button>
        <el-button type="primary" @click="save3"></el-button>
      </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import Person from "@/views/Person";
import request from "@/utils/request";
export default {
  name: "submit",
  components: {Person},
  data(){
    return{
      grade:'',
      form:{},
      form1:{

      },
      form2:{
        "":{},
        "":{}
      },
      form3:{
        "":{},
        "":{}
      },
      person:{},
      dialogVisible1:false,
      dialogVisible2:false,
      dialogVisible3:false,
      rules: {
        : [
          { required: true, message: '', trigger: 'blur' },
          { validator: this.validateNumberNotZero, trigger: 'blur' }
        ]
      }
    }
  },
  created(){
    let str = sessionStorage.getItem("student")||"{}"
    this.person = JSON.parse(str)
    this.choose()
  },
  methods:{
    save1(){
      this.form1[""] = this.person.sname;
      this.form1[""] = this.person.sno;
      this.form1[""] = this.person.syear;
      request.post("/submit/g1",this.form1).then(res =>{
        console.log(this.form1)
        console.log(this.res)
        this.$message({type: "success", message:""})
      })
      this.dialogVisible1 = false
    },
    save2(){
      this.form2[""] = this.person.sname;
      this.form2[""] = this.person.sno;
      this.form2[""] = this.person.syear;
      request.post("/submit/g2",this.form2).then(res =>{
        console.log(this.form2)
        console.log(this.res)
        this.$message({type: "success", message:""})
      })
      this.dialogVisible2 = false
    },
    save3(){
      this.form3[""] = this.person.sname;
      this.form3[""] = this.person.sno;
      this.form3[""] = this.person.syear;
      request.post("/submit/g3",this.form3).then(res =>{
        console.log(this.form3)
        console.log(this.res)
        this.$message({type: "success", message:""})
      })
      this.dialogVisible3 = false
    },
    choose() {
      if (this.person.sgrade === "") {
        this.dialogVisible1 = true;
      } else if (this.person.sgrade === "") {
        this.dialogVisible2 = true;
      } else {
        this.dialogVisible3 = true;
      }
    },
    validateNumberNotZero(rule, value, callback) {
      if (value === 0) {
        callback(new Error('0'));
      } else {
        callback();
      }
    },
  }
}

</script>

